import React, { useState } from "react";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import DeleteIcon from "@mui/icons-material/Delete";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Buffer } from "buffer";
import jwt_decode from "jwt-decode";
import Box from "@mui/material/Box";
import LinearProgress from "@mui/material/LinearProgress";
var format = require("xml-formatter");

toast.configure();

export default function Util1() {
  const [textValue, setTextValue] = useState("");
  const [privateKey, setPrivateKey] = useState("");
  const [publicKey, setPublicKey] = useState("");
  const [pubKeyWindow, setPubKeyWindow] = useState(false);
  const [privateKeyWindow, setPrivateKeyWindow] = useState(false);
  const [loadingBar, setLoadingBar] = useState(false);
  function formatText() {
    if (textValue !== "") {
      let newText1 = textValue.replace(/(\r\n|\n|\r)/gm, "");
      let newText2 = newText1.split(/[ ]+/);
      setTextValue(newText2.join(""));
    } else {
      toast.error("Provide Valid input!!", {
        autoClose: 2000,
        position: toast.POSITION.TOP_CENTER,
      });
    }
    setPubKeyWindow(false);
    setPrivateKeyWindow(false);
  }

  function handleCopy() {
    if (textValue !== "") {
      navigator.clipboard.writeText(textValue);
      toast.info("Text Copied!!", {
        autoClose: 2000,
      });
    } else {
      toast.error("No text to copy!!", {
        autoClose: 2000,
        position: toast.POSITION.TOP_CENTER,
      });
    }
  }

  function testJSON(text) {
    if (typeof text !== "string") {
      return false;
    }
    try {
      var json = JSON.parse(text);
      return typeof json === "object";
    } catch (error) {
      return false;
    }
  }

  function formatJSON() {
    if (!testJSON(textValue)) {
      toast.error("Provide valid JSON", {
        position: toast.POSITION.TOP_CENTER,
        autoClose: 2000,
      });
    } else {
      let Text = JSON.stringify(JSON.parse(textValue), null, 4);
      setTextValue(Text);
    }
  }

  function decodeBase64() {
    const regex =
      /^([A-Za-z0-9+/]{4})*([A-Za-z0-9+/]{3}=|[A-Za-z0-9+/]{2}==)?$/;

    if (regex.test(textValue) && textValue !== "") {
      let text = Buffer.from(textValue, "base64").toString("ascii");
      setTextValue(text);
    } else {
      toast.error("Provide valid base64 encoded string!!", {
        autoClose: 2000,
        position: toast.POSITION.TOP_CENTER,
      });
    }
    setPubKeyWindow(false);
    setPrivateKeyWindow(false);
  }

  function encodeBase64() {
    if (textValue !== "") {
      let text = Buffer(textValue).toString("base64");
      setTextValue(text);
    } else {
      toast.error("Provide valid input!!", {
        autoClose: 2000,
        position: toast.POSITION.TOP_CENTER,
      });
    }
    setPubKeyWindow(false);
    setPrivateKeyWindow(false);
  }

  function formatFingerPrint() {
    const regex = /^[A-Za-z0-9]*$/;
    if (textValue !== "" && regex.test(textValue)) {
      var text = textValue.toUpperCase();
      var Array = text.match(/.{1,2}/g);
      setTextValue(Array.join(":"));
    } else {
      toast.error("Provide valid input!!", {
        autoClose: 2000,
        position: toast.POSITION.TOP_CENTER,
      });
    }
    setPubKeyWindow(false);
    setPrivateKeyWindow(false);
  }

  function decodeJWT() {
    if (textValue !== "") {
      try {
        var decodedBody = jwt_decode(textValue);
        var decodedHeader = jwt_decode(textValue, { header: true });
        var finalJson = {
          header: decodedHeader,
          body: decodedBody,
        };
        setTextValue(JSON.stringify(finalJson));
      } catch {
        toast.error("Provided JWT is not valid!!", {
          position: toast.POSITION.TOP_CENTER,
          autoClose: 2000,
        });
      }
    } else {
      toast.error("Provide valid input!!", {
        position: toast.POSITION.TOP_CENTER,
        autoClose: 2000,
      });
    }
  }

  function formatXML() {
    if (textValue !== "") {
      try {
        var formattedXml = format(textValue);
        setTextValue(formattedXml);
      } catch {
        toast.error("Provided XML is not valid!!", {
          position: toast.POSITION.TOP_CENTER,
          autoClose: 2000,
        });
      }
    } else {
      toast.error("Provide valid input!!", {
        position: toast.POSITION.TOP_CENTER,
        autoClose: 2000,
      });
    }
    setPubKeyWindow(false);
    setPrivateKeyWindow(false);
  }

  function createJWT() {
    setLoadingBar(true);
    if (!testJSON(textValue)) {
      toast.error(
        "Provide valid JSON",
        {
          position: toast.POSITION.TOP_CENTER,
          autoClose: 2000,
        },
        setLoadingBar(false)
      );
    } else {
      fetch("https://create-jwt-v2.herokuapp.com/sendData", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          json: textValue,
        }),
      }).then((response) => {
        if (response.status === 200) {
          response.json().then((res) => {
            setTextValue(res.jwt);
            setPrivateKey(res.privateKey);
            setPublicKey(res.publicKey);
            setPubKeyWindow(true);
            setPrivateKeyWindow(true);
            setLoadingBar(false);
          });
        } else {
          toast.error(
            "Server is not responding. Please try again after some time.",
            {
              position: toast.POSITION.TOP_CENTER,
              autoClose: 2000,
            },
            setLoadingBar(false)
          );
        }
      });
    }
  }
  function clearWindow() {
    setTextValue("");
    setPublicKey("");
    setPrivateKey("");
    setPubKeyWindow(false);
    setPrivateKeyWindow(false);
  }

  return (
    <Grid container spacing={2} padding="50px">
      {loadingBar === true ? (
        <>
          <Box sx={{ width: "100%" }}>
            <LinearProgress color="success" />
          </Box>
        </>
      ) : null}
      <Grid item xs={12} md={3} sm={3}>
        <div className="container">
          <Button
            variant="contained"
            color="primary"
            style={{ minWidth: 170 }}
            onClick={formatText}
          >
            Single Line Text
          </Button>
        </div>
        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ minWidth: 170 }}
            onClick={formatJSON}
          >
            Prettify JSON
          </Button>
        </div>

        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ minWidth: 170 }}
            onClick={decodeBase64}
          >
            Decode Base64
          </Button>
        </div>
        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ minWidth: 170 }}
            onClick={encodeBase64}
          >
            Encode Base64
          </Button>
        </div>
        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ maxWidth: 170, minWidth: 170 }}
            onClick={formatFingerPrint}
          >
            Format Fingerprint
          </Button>
        </div>
        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ minWidth: 170 }}
            onClick={decodeJWT}
          >
            Decode JWT
          </Button>
        </div>

        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ maxWidth: 170, minWidth: 170 }}
            onClick={createJWT}
          >
            Create Sample JWT
          </Button>
        </div>
        <div className="container my-3">
          <Button
            variant="contained"
            color="primary"
            style={{ minWidth: 170 }}
            onClick={formatXML}
          >
            Prettify XML
          </Button>
        </div>
      </Grid>

      <Grid item xs={12} md={9} sm={9}>
        <TextField
          multiline
          fullWidth
          rows={20}
          label="Enter Text Here"
          id="inputText"
          inputProps={{ spellCheck: "false" }}
          value={textValue}
          onChange={(event) => setTextValue(event.target.value)}
        />

        <Grid container spacing={2} padding="10px">
          <Grid item xs={6} md={6} sm={6} className="col-md-12 text-center">
            <Button
              variant="outlined"
              color="error"
              style={{ minWidth: 100 }}
              startIcon={<DeleteIcon />}
              onClick={clearWindow}
            >
              Clear
            </Button>
          </Grid>

          <Grid item xs={6} md={6} sm={6} className="col-md-12 text-center">
            <Button
              variant="outlined"
              color="success"
              style={{ minWidth: 100 }}
              onClick={handleCopy}
            >
              Copy
            </Button>
          </Grid>
        </Grid>
        <Grid container spacing={2} padding="10px">
          {pubKeyWindow === true && privateKeyWindow === true ? (
            <>
              <Grid item xs={12} md={6} sm={6}>
                <TextField
                  multiline
                  fullWidth
                  InputProps={{
                    readOnly: true,
                  }}
                  rows={20}
                  label="Private Key"
                  id="inputText"
                  inputProps={{ spellCheck: "false" }}
                  value={privateKey}
                  onChange={(event) => setTextValue(event.target.value)}
                />
              </Grid>
              <Grid item xs={12} md={6} sm={6}>
                <TextField
                  multiline
                  fullWidth
                  rows={20}
                  InputProps={{
                    readOnly: true,
                  }}
                  label="Public Key"
                  id="inputText"
                  inputProps={{ spellCheck: "false" }}
                  value={publicKey}
                  onChange={(event) => setTextValue(event.target.value)}
                />
              </Grid>
            </>
          ) : null}
        </Grid>
      </Grid>
      <p>
        {
          textValue.split(/\s+/).filter((element) => {
            return element.length !== 0;
          }).length
        }{" "}
        words and {textValue.length} characters
      </p>
    </Grid>
  );
}
